.about-experience-card {
  width: 75%;
  margin: 2rem auto;
  padding: 3rem 6rem;
  box-shadow: 1rem 1rem 4rem rgba(0, 0, 0, 0.8);
  font-size: 2.2rem;
  color: white;
  cursor: pointer;
}
.about-experience-card h3 {
  margin: 1rem 0;
  text-transform: uppercase;
}

.about-experience-card span {
  display: block;
  margin: 1rem 0;
  font-size: 1.8rem;
}

.about-experience-card h4 {
  margin: 1rem 0;
}

.about-experience-card p {
  margin: 1rem 0;
  text-align: justify;
  font-weight: 200;
}

.about-experience-card:hover {
  transform: scale(1.05);
}

@media (max-width: 800px) {
  .about-experience-card {
    width: 100%;
  }
}
