@keyframes moveInHome {
  0% {
    opacity: 0;
    transform: translateY(-3rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}

.home-container {
  display: flex;
  flex: 1;
  flex-grow: 1;
  flex-direction: column;
}

.introduction {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-content: center;
  flex: 1;
  animation: moveInHome 0.5s ease-out 0.75s;
  animation-fill-mode: backwards;
  margin-bottom: 3rem;
}

.introduction .myself {
  display: flex;
  flex-direction: column;
  flex: 1;
  flex-grow: 1;
}

.introduction .myself h1 {
  color: white;
  font-size: 2.6rem;
  text-align: left;
  margin: 2rem 0 0.5rem;
  letter-spacing: 0.5rem;
  text-decoration: underline;
  font-weight: bold;
}
.introduction .myself h2 {
  color: white;
  font-size: 2.4rem;
  text-align: left;
  font-weight: 300;
  margin: 0 0 0.5rem;
  text-decoration: underline;
}

.introduction .myself > span {
  color: white;
  font-size: 2.2rem;
  text-align: left;
  font-weight: 300;
  margin: 0 0 0.5rem;
  text-decoration: underline white;
  line-height: 1.7;
}

.introduction .myself .tecnologies {
  display: flex;
  margin-top: 4rem;
}
.introduction .myself .tecnologies span {
  border-radius: 20px;
  padding: 8px 16px;
  background-color: var(--primary-color);
  font-size: 2rem;
  font-weight: 500;
  color: white;
  box-shadow: 10px 10px 1px rgba(0, 0, 0, 0.5);
  cursor: pointer;
  margin: auto 1rem;
  text-align: center;
}

.introduction .myself .tecnologies span:hover {
  animation: tecnologiesHover 1s;
  animation-fill-mode: forwards;
}

.introduction .photo {
  display: flex;
  align-items: center;
  justify-content: center;
}

.introduction .photo img {
  border: 3px solid var(--primary-color);
  text-align: center;
  height: 20rem;
  width: 20rem;
  object-fit: cover;
  border-radius: 50%;
}

@media (max-width: 800px) {
  .introduction {
    display: flex;
    flex-direction: column;
  }

  .introduction .photo {
    display: flex;
    flex: 1;
    flex-grow: 1;
    order: 1;
    justify-content: center;
  }
  .introduction .myself {
    order: 2;
    align-items: center;
  }
}

@keyframes tecnologiesHover {
  0% {
  }
  80% {
    transform: scale(1.1);
  }
  100% {
    font-weight: bold;
    color: var(--primary-color);
    transform: scale(1.05);
    box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.5);

    background-color: transparent;
  }
}
