@keyframes moveIn {
  0% {
    opacity: 0;
    transform: translateY(3rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  animation: moveIn 0.5s ease-out 0.75s;
  animation-fill-mode: backwards;
}

.header-logo img {
  height: 15rem;
}

.header-links nav a {
  font-size: 2.4rem;
  margin-left: 2rem;
  text-decoration: none;
  color: white;
  padding: 1rem;
}


.header-links nav a:after, .header-links nav a:hover{
  font-weight: 700;

  content: "";
  border-bottom: 2px solid white;
}
